@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  width: 374px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px auto 60px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 14px;
  height: 16px;
  margin-left: 0px;
  margin-right: 16px;
  color: #000000;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
  text-decoration: none;
  vertical-align: middle;

  //   border: 1px solid #ddd;
  //   border-width: 1px 0;
}

.tui-pagination .tui-page-btn:hover {
  // background-color: #cececec7;
  border-radius: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  //   border-color: #4b96e6;
  cursor: default;
  width: 40px;
  height: 40px;
  padding-top: 12px;
  border-radius: 5px;
}

.tui-pagination .tui-is-selected:hover {
  // background-color: #ff893b;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  //   border-left: 1px solid #ddd;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  //   border-right: 1px solid #ddd;
}

.tui-pagination .tui-first-child.tui-is-selected {
  //   border-left: 1px solid #4b96e6;
}

.tui-pagination .tui-last-child.tui-is-selected {
  //   border-right: 1px solid #4b96e6;
}

// .tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next 
  // .tui-pagination .tui-last,
  // .tui-pagination .tui-prev-is-ellip,
  // .tui-pagination .tui-next-is-ellip
{
  width: 40px;
  height: 40px;

  background: #f7f7f7;
  border-radius: 5px;

  overflow: hidden;
  padding: 0;
  font-size: 0;
  line-height: 40px;
  white-space: nowrap;
  font-weight: normal;
  //   border: 1px solid #ddd;
}

.tui-pagination .tui-first {
  display: inline-block;
}
.tui-pagination .tui-last {
  display: inline-block;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
  // display: none;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
  // display: none;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

