  width: 11px;
  height: 10px;
  // background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 16px;
  height: 16px;

  // background-position: -10px -10px;
  background: url('../images/pagination/arrow-left.svg') center;

  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  // stroke: #ff6b08;
  // fill: blue;
}

.tui-pagination .tui-ico-next {
  width: 16px;
  height: 16px;
  // background-position: -13px -24px;
  background: url('../images/pagination/arrow-right.svg') center;

  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
  // stroke: #ff6b08;
