.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  // padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #ff6b08;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  // padding: 8px 0 6px;
  vertical-align: top;
}

.tui-pagination .tui-page-btn:nth-child(9) {
  margin-right: 10px;
}

// .tui-page-btn .tui-next-is-ellip .custom-class-next .tui-last-child {
//   margin-right: 10px;
// }

.tui-pagination .tui-page-btn:nth-child(10) {
  margin-right: 10px;
}

.tui-pagination .tui-page-btn:nth-child(11) {
  margin-right: 0px;
}

@media screen and (max-width: 768px) {
  .tui-pagination {
    width: 280px;
    margin: 40px auto;
  }

  .tui-pagination .tui-page-btn:nth-child(9) {
    margin-right: 0;
  }

  .tui-page-btn.tui-next.custom-class-next {
    margin-right: 0;
  }

  .tui-pagination .tui-first {
    display: none;
  }
  .tui-pagination .tui-last {
    display: none;
  }

  .tui-pagination .tui-prev-is-ellip {
    border-right: 0;
    display: none;
  }

  .tui-pagination .tui-next-is-ellip {
    border-left: 0;
    display: none;
  }
}
