:root {
  // fonts
  --font-main: 'Roboto', sans-serif;

  // colors
  --txt-main-color: #000;
  --txt-second-color: #8c8c8c;
  --bg-main-color: #fff;
  --bg-second-color: #f7f7f7;
  --accent-color: #ff6b01;
  --border-color: #000;

  --txt-dark-theme: #fff;
  --bg-modal-dark-theme: #333;
  --bg-second-dark-theme: #8c8c8c;
  --bg-third-dark-theme: #f7f7f7;

  // animation
  --time: 200ms;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-movie {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 280px;
  background-color: var(--bg-main-color);
  padding: 48px 20px 40px 20px;

  font-family: var(--font-main);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 133%;
  color: var(--txt-main-color);

  max-height: 100vh;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 0;
  }

  @media screen and (min-width: 768px) {
    flex-direction: row;
    width: 618px;
    padding: 42px 30px 40px 30px;
  }

  @media screen and (min-width: 1024px) {
    flex-direction: row;
    width: 882px;
    padding: 50px 30px 40px 30px;
  }
}

///// Movie poster container /////

.modal-movie__poster {
  position: relative;
  display: inline-block;
  height: min-content;
  background: #000;
  border-radius: 5px;

  @media screen and (max-width: 767.9px) {
    width: 100%;
    margin-bottom: 30px;
  }

  @media screen and (min-width: 768px) {
    width: 264px;
    margin-right: 30px;
  }

  @media screen and (min-width: 1024px) {
    width: 304px;
  }
}

.modal-movie__img {
  width: inherit;
  border-radius: 5px;
  cursor: pointer;
  transition: all var(--time) var(--timing-function);
}

.btn-trailer {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 68px;
  height: 48px;
  background-color: transparent;
  opacity: 0.8;
  pointer-events: none;
  transition: all var(--time) var(--timing-function);
}

.btn-trailer__path {
  transition: all var(--time) var(--timing-function);
}

.modal-movie__poster:hover,
.modal-movie__poster:focus {
  .modal-movie__img {
    opacity: 0.8;
  }

  .btn-trailer {
    opacity: 1;
  }

  .btn-trailer__path {
    fill: #f00;
  }
}

///// Movie content container /////

.modal-movie__content {
  display: inline-block;
  width: 100%;
  height: max-content;
}

.modal-movie__title {
  font-weight: inherit;
  font-size: 20px;
  line-height: 117%;
  text-transform: uppercase;
  margin-bottom: 20px;

  @media screen and (min-width: 1024px) {
    font-size: 30px;
  }
}

.modal-movie-info {
  width: 100%;
  margin-bottom: 20px;
}

.modal-movie-info__title {
  vertical-align: top;
  width: 40%;
  color: var(--txt-second-color);

  &:not(:last-child) {
    padding-bottom: 10px;
  }
}

.modal-movie-info__value {
  vertical-align: top;
  padding: 0;

  &:not(:last-child) {
    padding-bottom: 10px;
  }
}

.modal-movie-info__rate {
  display: inline-block;
  text-align: center;
  width: 36px;
  padding: 0.5px 4px;
  background-color: var(--bg-second-color);
  border-radius: 5px;
  color: #000000;

  &:first-child {
    color: var(--bg-main-color);
    background-color: var(--accent-color);
  }
}

.modal-movie-about {
  margin-bottom: 20px;
}

.modal-movie-about__title {
  font-weight: inherit;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.modal-movie-about__desc {
  line-height: 167%;
}

.modal-movie-btn-wrapper {
  display: flex;

  @media screen and (max-width: 1024px) {
    justify-content: space-between;
  }
}

.modal-movie-btn {
  display: inline-block;
  width: calc((100% - 15px) / 2);
  min-height: 44px;
  padding: 0 5px;
  border: 1px solid var(--border-color);
  border-radius: 5px;
  background-color: transparent;
  font-weight: inherit;
  text-transform: uppercase;
  color: var(--txt-main-color);
  cursor: pointer;

  transition: all var(--time) var(--timing-function);

  @media screen and (min-width: 1024px) {
    width: 136px;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

.active {
  border: 1px solid var(--accent-color);
  background: var(--accent-color);
  padding: 0;
}

.modal-movie-btn:hover,
.modal-movie-btn:focus {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
  color: var(--bg-main-color);
  &:hover,
  &:focus {
    background-color: var(--accent-color);
    border-color: var(--accent-color);
    color: var(--bg-main-color);
  }
}

.modal-movie-close {
  position: absolute;
  right: 10px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border: none;
  border-radius: 50%;
  background-color: var(--bg-second-color);
  cursor: pointer;

  background-image: url('../images/modal-movie/close.svg');
  background-position: center;
  background-repeat: no-repeat;

  &:hover,
  &:focus {
    transform: scale(1.1);
    background-color: var(--accent-color);
  }

  transition: transform 0.05s, background-color var(--timing-function);
}

////////// Dark theme //////////

body.body-theme {
  .modal-movie {
    background-color: var(--bg-modal-dark-theme);
    color: var(--txt-dark-theme);
  }

  .modal-movie-btn {
    background-color: var(--bg-second-dark-theme);
    border: none;
    &:hover,
    &:focus {
      background-color: var(--accent-color);
    }
  }

  .modal-movie-close {
    background-color: var(--bg-second-dark-theme);
    &:hover,
    &:focus {
      background-color: var(--accent-color);
    }
  }
}

